@import "../../global.scss";

.menu{
    width: 300px;
    height: 100vh;
    background-color: rgba(51 , 51, 51, 0.6);
    position: fixed;
    top:0;
    right: -300px;
    z-index: 2;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    transition: all 1s ease;
    //opacity: 0.6;

    &.active{
        right: 0;
    }

    ul{
        margin: 0;
        padding: 0;
        font-size: 30px;
        color: wheat;
        list-style: none;
        font-weight: 300;
        width: 60%;

        li{
            
            margin-bottom: 30px;
            a{
                font-size: inherit;
                color: inherit;
                text-decoration: none;
            }

            &:hover{
                font-weight: 500;
            }
        }

    }


}